@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+KR:wght@300;400&family=Montserrat&display=swap");

body {
  font-family: "IBM Plex Sans KR", "Bahnschrift", sans-serif;
  color: #f8f8f2;
  background-color: #191a21;
  word-break: keep-all;
  white-space: pre-line;
  text-align: center;
  font-size: 16px;
}

h1 {
  font-family: "Montserrat", "IBM Plex Sans KR", sans-serif;
  font-size: 20px;
  font-weight: bold;
  color: #f1fa8c;
}
