.hidden {
  display: none;
}

/* Dracula Colorscheme */
.bg-current-line {
  background-color: #44475a;
}

.bg-cyan {
  background-color: #8be9fd;
}

.bg-green {
  background-color: #50fa7b;
}

.bg-orange {
  background-color: #ffb86c;
}

.bg-pink {
  background-color: #ff79c6;
}

.bg-purple {
  background-color: #bd93f9;
}

.bg-red {
  background-color: #ff5555;
}

.bg-yellow {
  background-color: #f1fa8c;
}

/* End of Dracula Colorscheme */

.cat-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 80%;
}

html {
  height: 100%;
  overflow-x: hidden;
}

a,
a:active {
  color: #56fa77;
  text-decoration: none;
}

a:hover {
  color: #f1fa8c;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

