.header {
  font-size: 32px;
  text-align: center;
  margin-top: 20px;
  margin-left: -0%;
}

@media (min-width: 768px) {
  .header {
    font-size: 56px;
  }
}

.content-panel {
  width: 90%;
  margin-left: 5%;
  margin-top: 20px;
  background-color: #282a36;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 30px;
  padding-right: 30px;
  box-shadow: 0px 0px 0px 0px, 0px 6px 12px rgba(0, 0, 0, 0.2);
  border-bottom: 10px solid #56fa77;
}

i[class~="fab"] {
  color: #f8f8f2;
  font-size: 24px;
}

.index {
  height: 100%;
  max-height: 100%;
  width: 100%;
  padding: 0px;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 768px) {
  .index {
    width: 780px;
    max-width: 780px;
  }
}

@media (min-width: 1200px) {
  .index .header {
    margin-left: -10%;
    width: 120%;
  }
}

.index .row {
  height: 100%;
  max-height: 100%;
  margin-top: 60px;
  margin-left: 0px;
  margin-right: 0px;
}

.index .avatar {
  width: 100%;
  margin-top: -80px;
  text-align: center;
}

.index .articles {
  text-align: center;
  font-size: 20px;
}

.index .articles h2 {
  font-family: "Montserrat", sans-serif;
  margin-bottom: 30px;
  color: #56fa77;
  font-size: 26px;
}

.index .articles ul {
  padding: 0px;
}

.index .articles ul li {
  margin-bottom: 20px;
  font-size: 24px;
  font-family: "IBM Plex Sans KR Light";
}

.index .articles ul li a:hover {
  color: #f1fa8c;
}

.index .articles ul li small {
  font-size: 13px;
  margin-top: -10px;
  padding-top: 0px;
  color: #ff79c6;
}

.index .articles a,
.index .articles a:active {
  color: #ffffff;
  white-space: nowrap;
}

.index .articles a:hover {
  color: #56fa77;
}

@media (min-width: 768px) {
  .all-posts #search-container {
    padding-top: 75px;
    padding-bottom: 150px;
  }
}

@media (min-width: 992px) {
  .all-posts {
    margin-left: -15px;
    margin-right: -15px;
  }

  .all-posts #search-container {
    padding-bottom: 300px;
  }
}

@media (min-width: 992px) {
  .all-posts .content-panel {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media (min-width: 768px) {
  .single p {
    margin-bottom: 20px;
  }
}

@media (min-width: 992px) {
  .single .article {
    width: 70%;
    margin-left: 15%;
  }
}

@media (min-width: 1200px) {
  .single .article {
    width: 50%;
    margin-left: auto;
    margin-right: auto;
  }

  .feedback {
    width: 50%;
    margin-left: auto;
    margin-right: auto;
  }
}

.single .article .thumb {
  margin-top: 20px;
  margin-bottom: 0px;
  text-align: center;
}

.single .article .thumb img {
  height: 50px;
}

@media (min-width: 768px) {
  .single .article .thumb img {
    height: 70px;
  }
}

@media (min-width: 768px) {
  .single .article .read-time {
    margin-bottom: 40px;
  }
}

@media (min-width: 768px) {
  .single .article .content {
    font-size: 20px;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 40px;
    padding-bottom: 20px;
  }
}

.single .article .content img {
  max-width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 768px) {
  .single .article .content blockquote {
    margin-left: -30px;
    padding-left: 45px;
    padding-right: 15px;
  }
}

@media (min-width: 768px) {
  .single .article .content figure.highlight,
  .single .article .content div.highlighter-rouge {
    margin-left: -30px;
    padding-right: 60px;
  }
}

@media (min-width: 768px) {
  .single .article .content figure.highlight pre,
  .single .article .content div.highlighter-rouge pre {
    padding-left: 50px;
    padding-right: 10px;
    margin-bottom: 40px;
    font-size: 15px;
  }
}

@media (min-width: 768px) {
  .related .title {
    font-size: 36px;
  }
}

