footer div {
  color: #030303;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

.btn-categories {
  background-color: #56fa77;
  border-color: #56fa77;
  color: #ffffff;
}

.btn-categories:hover {
  background-color: #f1fa8c;
  border-color: #f1fa8c;
  color: #ffffff;
}

.btn-categories:active {
  background-color: #f1fa8c;
  border-color: #f1fa8c;
  color: #ffffff;
}

.btn-categories:focus {
  background-color: #f1fa8c;
  border-color: #f1fa8c;
  color: #ffffff;
}

table {
  margin: 0 auto;
  border-collapse: collapse;
  border-spacing: 0;
  border: 2px solid #21211e;
}

th {
  padding: 2px;
  border: 2px solid #21211e;
}

td {
  padding: 2px;
  border: 1px solid #f1fa8c;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.hide-scrollbar::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.hide-scrollbar {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
